body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{ 
  margin: 0;
}
.app {
  text-align: center;
}


.login {
    background-color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.login_container{
    width:300px;
    height:-webkit-fit-content;
    height:-moz-fit-content;
    height:fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid lightgray;
    padding: 20px;
}

.login_container > h1 {
    font-weight: 500;
    margin-bottom: 20px;
}

.login_container > form > h5 {
    margin-bottom: 5px;
}
.header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 130px;
  background-color: #00274c;
  font-size: large;
  font-weight: bold;
  /* height: 400px;
    width: 100%; */
  /* background-image:url('https://news.fullerton.edu/app/uploads/2021/04/campus-gateway.jpeg'); */
}

.header_logo {
  width: 200px;
  object-fit: contain;
  margin: 0 20px;
  margin-top: 10px;
}

.header_search {
  display: flex;
  flex: 1 1;
  align-items: center;
  border-radius: 24px;
}

.header_searchInput {
  height: 12px;
  padding: 10px;
  border: none;
  width: 100%;
}

.log_in {
  color: white;
}

.header_nav {
  display: flex;
  align-items: center;
  margin-left: auto;
  padding: 20px;
  grid-column-gap: 1em;
  -webkit-column-gap: 1em;
          column-gap: 1em;
  color: white;
}

a {
  color: white;
  text-decoration: none;
}

.home_Container {
  display: flex;
  background-color: rgb(241, 241, 241);
  flex-direction: column;
  align-content: center;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.home_Row {
  background-color: white;
  display: flex;
  flex-direction: row;
  margin-left: auto;
  margin-right: auto;
  border: 2px solid;
  margin-top: 20px;
  margin-bottom: 10px;
  width: 80%;
}

.homepage_image {
  opacity: 1;
  display: block;
  align-content: center;
  margin-right: 10px;
  width: 100%;
  /* z-index: -1; */
  /* mask-image: linear-gradient(black, transparent); */
}

.product_Container{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: white;

}

.product_Container > img{
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}
